}
@media (max-width: 1024px) {
  [data-flex],
[data-flex=row] {
    box-sizing: border-box;
    margin: 0 auto;
    width: 100%;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex: 0 1 auto;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

[data-flex=column] {
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  flex-wrap: wrap;
}

[data-column="1"] {
  box-sizing: border-box;
  flex-basis: calc(100% / 12 * 1);
  max-width: calc(100% / 12 * 1);
  padding: 0;
}

