  padding: 0 var(--space-phi);
}

[data-gutter=y] {
  padding: calc(var(--space-e) / 2) 0;
}

[data-gutter="x/y"] {
  padding: calc(var(--space-e) / 2) var(--space-phi);
}

@keyframes menuLink--before {
  0% {
    filter: blur(1px);
  }
  50% {
    filter: blur(2px);
  }
  100% {
    filter: blur(1px);
  }
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes moveDots {
  from {
    transform: rotate(0deg) scale(12) translateX(-20px);
  }
  to {
    transform: rotate(360deg) scale(18) translateX(20px);
  }
}
@keyframes textMorph3d {
  0% {
    text-shadow: 2px 2px 0px rgb(40, 40, 40), 0px 0px 2px rgba(10, 189, 240, 0.05), 0px 0px 0px rgba(254, 1, 1, 0.08);
  }
  50% {
    text-shadow: 2px 2px 0px rgb(40, 40, 40), -5px -2px 0px rgba(10, 189, 240, 0.1), 4px 4px 1px rgba(254, 1, 1, 0.15);
  }
  100% {
    text-shadow: 2px 2px 0px rgb(40, 40, 40), 4px 3px 1px rgba(10, 189, 240, 0.04), -1px 3px 2px rgba(254, 1, 1, 0.05);
  }
}
@keyframes hueRotator {
  0% {
    backdrop-filter: blur(0px) contrast(1) hue-rotate(0);
  }
  50% {
    backdrop-filter: blur(3px) contrast(1.2) hue-rotate(180deg);
  }
  100% {
    backdrop-filter: blur(0px) contrast(1) hue-rotate(0);
  }
}
@keyframes hueRotatorBlur {
  0% {
    backdrop-filter: blur(9px) contrast(0.8) hue-rotate(0);
  }
  50% {
    backdrop-filter: blur(6px) contrast(1.8) hue-rotate(120deg);
  }
  100% {
    backdrop-filter: blur(9px) contrast(0.8) hue-rotate(0);
  }
}
@keyframes line-top-in {
  0% {
    left: -5px;
    bot: 0;
    transform: rotate(-45deg);
  }
  20% {
    left: -5px;
    bot: 0;
    transform: rotate(-60deg);
  }
  80% {
    left: 0;
    bot: 0;
    transform: rotate(15deg);
  }
  100% {
    left: 0;
    bot: 1px;
    transform: rotate(0deg);
  }
}
@keyframes line-top-out {
  0% {
    left: 0;
    top: 0;
    transform: rotate(0deg);
  }
  20% {
    left: 0;
    top: 0;
    transform: rotate(15deg);
  }
  80% {
    left: -5px;
    top: 0;
    transform: rotate(-60deg);
  }
  100% {
    left: -5px;
    top: 1px;
    transform: rotate(-45deg);
  }
}
@keyframes line-bot-in {
  0% {
    left: -5px;
    transform: rotate(45deg);
  }
  20% {
    left: -5px;
    bot: 0;
    transform: rotate(60deg);
  }
  80% {
    left: 0;
    bot: 0;
    transform: rotate(-15deg);
  }
  100% {
    left: 0;
    transform: rotate(0deg);
  }
}
@keyframes line-bot-out {
  0% {
    left: 0;
    transform: rotate(0deg);
  }
  20% {
    left: 0;
    transform: rotate(-15deg);
  }
  80% {
    left: -5px;
    transform: rotate(60deg);
  }
  100% {
    left: -5px;
    transform: rotate(45deg);
  }
}
.hidden,
.visuallyhidden,
.hide {
  display: none;
  visibility: hidden;
  backface-visibility: hidden;
  border: 0;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.pointer {
  cursor: pointer;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.burn-overlay {
  @apply flex w-full h-full absolute top-0 left-0;
  background: rgba(4, 4, 5, 0.7);
  mix-blend-mode: color-burn;
  animation-name: hueRotatorBlur;
  animation-duration: 12s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
}

.blur-overlay {
  @apply flex w-full h-full absolute top-0 left-0;
  background: var(--gr-sunset-purple);
  backdrop-filter: blur(4px);
  opacity: 0.25;
}

.rgb-overlay {
  @apply flex w-full h-full absolute top-0 left-0;
  background: rgba(31, 39, 50, 0.3);
  mix-blend-mode: color-burn;
  animation-name: hueRotator;
  animation-duration: 8s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
}

@keyframes loadify {
  to {
    opacity: 1;
    visibility: visible;
    backface-visibility: visible;
  }
}
/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 112.8%;
  line-height: 1.618;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
img,
picture,
video,
canvas {
  transition: filter 300ms ease;
}

body {
  color: var(--ggs-text);
  font-size: var(--base);
  font-family: "Barlow", sans-serif;
  font-weight: 400;
  background-image: var(--ggs-bg);
  background-size: 100%;
  background-attachment: fixed;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
  font-feature-settings: "kern" 1;
  font-kerning: normal;
  @apply flex flex-col w-full h-full relative;
  block-size: 100%;
}
