}
blockquote.quote > cite {
  display: block;
  font-size: var(--sm);
  font-style: italic;
  font-weight: 400;
}
blockquote.quote > cite::before {
  content: "-";
  margin-right: 6px;
}
blockquote.quote::before {
  content: '"';
  position: absolute;
  z-index: -1;
  top: -1rem;
  left: 0;
  color: #495057;
  font-size: var(--xxxl);
}
blockquote.quote::after {
  content: '"';
  position: absolute;
  z-index: -1;
  bottom: 0;
  right: 0;
  color: #495057;
  font-size: var(--xxxl);
