.xc-header .xc-title__link {
  @apply block relative z-20 md:left-9 mix-blend-exclusion;
  font-size: var(--xxxl);
  font-family: "Blacklisted", sans-serif;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 0;
  transform: scaleY(0.7);
  animation-name: textMorph3d;
  animation-iteration-count: infinite;
  animation-fill-mode: both;
  animation-direction: alternate-reverse;
  animation-timing-function: cubic-bezier(0.5, 1, 0.75, 1.25);
  animation-duration: 250ms;
  transition: all 420ms ease-in-out;
}
@media (max-width: 48rem) {
  .xc-header .xc-title__link {
    font-size: 4rem;
  }
}
.xc-header .xc-title__link:hover {
  text-decoration: none;
  animation: none;
  text-shadow: var(--ggs-text-3D);
  transition: all 420ms ease-in-out;
}
.xc-header .xc-title__subtitle {
  @apply block relative left-16 lg:left-20 z-20;
  margin-top: -33px;
  color: #6D7682;
  font-family: "Lighters", sans-serif;
  font-weight: 600;
  font-size: var(--base);
  text-transform: uppercase;
}
.xc-header .xc-menu {
  @apply relative flex justify-center lg:justify-end items-center;
}

@media (max-width: 1024px) {
  .stick {
    @apply fixed w-full top-0 left-0;
    transition: all 300ms ease-in;
  }
  .stick .xc-navbar {
    @apply pb-2;
  }
}
@media (max-width: 1024px) and (max-width: 1024px) {
  .stick .xc-title__logo {
    width: 20rem;
    height: 20rem;
    top: -80px;
    left: -80px;
  }
  .stick .xc-title__logo::after {
    width: 18.944rem;
    height: 18.944rem;
  }
  .stick .xc-title__link {
    font-size: 3rem;
  }
  .stick .xc-title__subtitle {
    margin-top: -23px;
  }
}
@media (max-width: 1024px) {
  .stick .xc-nav__burger {
    top: -4.5rem;
  }
}

.xc-nav {
  @apply relative z-20 w-full xl:w-[90%];
  display: none;
  transition: all 400ms ease;
}
@media (min-width: 1024px) {
  .xc-nav {
    display: block;
  }
}
.xc-nav.expand {
  @apply flex fixed z-40 top-0 left-0 w-full h-screen justify-center items-center;
  transition: all 400ms ease;
  background-color: #040405;
}
.xc-nav.expand ul {
  @apply justify-start;
}
.xc-nav.expand ul li {
  padding: 1rem 0;
}
.xc-nav ul {
  @apply flex flex-col xl:flex-row justify-between items-center text-center;
}
.xc-nav ul li {
  flex: 0 1 0%;
}
.xc-nav ul li.nav-current a {
  color: var(--ggs-brighter);
}
.xc-nav ul li.nav-current a::before {
  content: "";
  position: absolute;
}
.xc-nav ul li a {
  @apply block relative;
  font-size: var(--p);
  font-family: "Lighters", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--ggs-link-inv);
  transition: all 200ms ease;
}
.xc-nav ul li a::after {
  @apply absolute left-0 bottom-[6px] w-full h-[1px];
  content: "";
  background: radial-gradient(circle, #da77f2 0%, rgba(51, 8, 103, 0.8) 50%, rgba(48, 207, 208, 0) 100%);
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 420ms ease-in;
}
.xc-nav ul li a:hover {
  color: var(--ggs-brighter);
  text-decoration: none;
}
.xc-nav ul li a:hover::after {
  transform: scaleX(1);
}
@media (min-width: 90rem) {
  .xc-nav ul li a {
    font-size: var(--md);
  }
}
.xc-nav__burger {
  @apply absolute z-50 -top-[7.5rem] right-4;
  height: 28px;
  width: 36px;
}
.xc-nav__burger .burger-box .line-top,
.xc-nav__burger .burger-box .line-middle,
.xc-nav__burger .burger-box .line-bottom {
  @apply absolute block rounded-sm bg-white;
  width: 36px;
  height: 4px;
}
.xc-nav__burger .burger-box .line-top {
  top: 0;
  transform-origin: 34px 2px;
}
.xc-nav__burger .burger-box .line-middle {
  top: 12px;
  transition: opacity 200ms linear;
}
.xc-nav__burger .burger-box .line-bottom {
  bottom: 0;
  transform-origin: 34px 2px;
}
.xc-nav__burger .burger-box.is-open .line-top {
  animation: line-top-out 400ms linear normal;
  animation-fill-mode: forwards;
}
.xc-nav__burger .burger-box.is-open .line-middle {
  opacity: 0;
}
.xc-nav__burger .burger-box.is-open .line-bottom {
  animation: line-bot-out 400ms linear normal;
  animation-fill-mode: forwards;
}
.xc-nav__burger .burger-box.is-closed .line-top {
  animation: line-top-in 400ms linear normal;
  animation-fill-mode: forwards;
}
.xc-nav__burger .burger-box.is-closed .line-middle {
  transition-delay: 100ms;
}
.xc-nav__burger .burger-box.is-closed .line-bottom {
  animation: line-bot-in 400ms linear normal;
  animation-fill-mode: forwards;
}

.xc-hero {
  @apply relative;
  border-image: linear-gradient(to left, rgba(192, 207, 223, 0.4), rgba(192, 207, 223, 0)) 1 0 1 0;
}
.xc-hero__wrapper {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .xc-hero__wrapper {
    max-width: calc(768px - (calc(var(--space-pi) / 2.71828) * 2));
  }
}
@media (min-width: 1024px) {
  .xc-hero__wrapper {
    max-width: calc(1024px - (calc(var(--space-pi) / 2.71828) * 2));
  }
}
@media (min-width: 1280px) {
  .xc-hero__wrapper {
    max-width: calc(1280px - (calc(var(--space-pi) / 2.71828) * 2));
  }
}
@media (min-width: 1400px) {
  .xc-hero__wrapper {
    max-width: calc(1400px - (calc(var(--space-pi) / 2.71828) * 2));
  }
}
@media (min-width: 1840px) {
  .xc-hero__wrapper {
    max-width: calc(1840px - (calc(var(--space-pi) / 2.71828) * 2));
  }
}

#about .xc-hero, #contact .xc-hero {
  @apply flex flex-row flex-wrap-reverse lg:flex-wrap lg:h-[22rem];
  background-color: #040405;
}
#about .xc-hero__block, #contact .xc-hero__block {
  @apply relative flex items-center lg:items-end justify-end;
}
#about .xc-hero__block h3, #contact .xc-hero__block h3 {
  @apply relative mt-4 lg:mt-0 mr-6;
