#about .xc-hero__image, #contact .xc-hero__image {
  @apply relative flex items-end justify-start;
}
#about .xc-hero__image figure, #contact .xc-hero__image figure {
  @apply relative w-full h-80 lg:h-full max-h-[540px] overflow-hidden;
}
#about .xc-hero__image figure img, #contact .xc-hero__image figure img {
  @apply w-full h-full object-cover object-bottom;
